body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  line-height: 20px;
  color: #323238;
  background-color: #fff; }

* {
  outline: none !important; }

a {
  text-decoration: none; }

ul,
ol {
  list-style: none; }

.container {
  position: relative;
  width: calc(100% - 76px);
  max-width: 1240px;
  margin: 0 auto; }

.wrapper {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto; }
  .wrapper-flex {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start; }
    .wrapper-flex .main {
      width: calc(100% - 340px);
      margin-left: 40px;
      box-sizing: border-box;
      padding-top: 40px; }
      .wrapper-flex .main .container {
        margin-left: 0; }
      @media (max-width: 1300px) {
        .wrapper-flex .main {
          width: 100%;
          padding-top: 103px;
          margin-left: 0; }
          .wrapper-flex .main .container {
            margin-left: auto;
            margin-right: auto;
            width: calc(100% - 78px); } }
    @media (max-width: 1300px) and (max-width: 580px) {
      .wrapper-flex .main .container {
        width: calc(100% - 30px); } }

