/* Blocks */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 50px;
  padding: 10px 22px;
  background-color: #fff;
  box-sizing: border-box; }
  .header__left, .header__right {
    display: flex;
    align-items: center; }
  .header__phone {
    display: flex;
    align-items: center; }
  .header__burger {
    display: flex;
    align-items: center;
    width: 31px;
    height: 18px;
    position: relative;
    cursor: pointer;
    transition-duration: 0.4s;
    margin-left: 64px; }
    .header__burger .icon-left {
      position: absolute;
      top: 6px;
      width: 19px;
      height: 3px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger .icon-left:before {
      content: "";
      width: 19px;
      height: 3px;
      position: absolute;
      top: -6px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger .icon-left:after {
      content: "";
      width: 19px;
      height: 3px;
      position: absolute;
      top: 6px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger .icon-right {
      position: absolute;
      top: 6px;
      left: 6px;
      width: 19px;
      height: 3px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger .icon-right:before {
      content: "";
      width: 19px;
      height: 3px;
      position: absolute;
      top: -6px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger .icon-right:after {
      content: "";
      width: 19px;
      height: 3px;
      position: absolute;
      top: 6px;
      transition-duration: 0.4s;
      background-color: #446cb2; }
    .header__burger.open .icon-left {
      transition-duration: 0.4s;
      background: transparent; }
    .header__burger.open .icon-left:before {
      transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px); }
    .header__burger.open .icon-left:after {
      transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px); }
    .header__burger.open .icon-right {
      transition-duration: 0.4s;
      background: transparent; }
    .header__burger.open .icon-right:before {
      transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px); }
    .header__burger.open .icon-right:after {
      transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px); }

